.navbar {
    background-color: #2c3e50;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.navbar-brand a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: bold;
}

.navbar-links {
    display: flex;
    gap: 2rem;
}

.nav-link {
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
    position: relative;
}

.nav-link:hover {
    color: #3498db;
}

.cart-badge {
    position: absolute;
    top: -8px;
    right: -12px;
    background-color: #e74c3c;
    color: white;
    border-radius: 50%;
    padding: 0.2rem 0.5rem;
    font-size: 0.8rem;
}

.cart-link {
    position: relative;
    padding-right: 10px;
}

@media (max-width: 768px) {
    .navbar {
        padding: 1rem;
    }
    
    .navbar-links {
        gap: 1rem;
    }
}
